:root {
  --width-left: 288px;
  --width-main: 720px;
  --gap-l: 16px;
  --gap-p: 0.75rem;
}
@media screen and (min-width: 2048px) {
  :root {
    --gap-l: 32px;
    --width-left: 352px;
    --width-main: 780px;
  }
}
@media screen and (min-width: 2560px) {
  :root {
    --width-main: 860px;
    --gap-l: 64px;
  }
}
@media screen and (max-width: 768px) {
  :root {
    --width-left: 252px;
  }
}
@media screen and (max-width: 667px) {
  :root {
    --width-left: 288px;
  }
}
:root {
  --site-bg: #f9fafb;
  --card: #fff;
  --block: #f1f2f3;
  --block-border: #e3e5e8;
  --block-hover: #e9eaec;
  --block-lighten: #fff;
  --text-p0: #000;
  --text-p1: #333;
  --text-p2: #4d4d4d;
  --text-p3: #808080;
  --text-p4: #999;
  --text-meta: #ccc;
  --text-code: #f53900;
  --theme-bg2: #1cd0fd;
  --theme-link: #2094f3;
}
@media (prefers-color-scheme: dark) {
  :root {
    --site-bg: #26292c;
    --card: #383f47;
    --block: #1c1e21;
    --block-border: #383d42;
    --block-hover: #151719;
    --block-lighten: #464c53;
    --text-p0: #fff;
    --text-p1: #ccc;
    --text-p2: #b3b3b3;
    --text-p3: #858585;
    --text-p4: #707070;
    --text-meta: #4d4d4d;
    --text-code: #ff6333;
  }
}
@media screen and (prefers-color-scheme: dark) and (max-width: 667px) {
  :root {
    --site-bg: #000;
  }
}
pre {
  font-family: Menlo, Monaco, Consolas, system-ui, "Courier New", monospace, sans-serif;
  font-size: 0.8125rem;
  tab-size: 4;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  -webkit-tab-size: 4;
}
a {
  text-decoration: none;
  color: var(--theme-link);
}
a:hover {
  color: #ff5724;
}
.md-text p:not([class]) {
  text-align: justify;
}
hr {
  color: var(--text-meta);
  opacity: 0.1;
}
img {
  max-width: 100%;
}
li {
  font-size: 0.9375rem;
}
ul,
ol {
  padding-left: 1.5rem;
}
table:not([class]) {
  border-collapse: collapse;
  overflow: auto;
  display: block;
  margin: 1rem 0;
  max-width: 100%;
  vertical-align: text-top;
  font-size: 0.875rem;
}
table:not([class]) th {
  background: var(--block);
}
table:not([class]) td,
table:not([class]) th {
  padding: 0.5em 1em;
  border: 1px solid var(--block-hover);
  line-height: 1.5;
}
table:not([class]) tr {
  word-break: keep-all;
  white-space: nowrap;
}
table:not([class]) tr:hover {
  background: var(--block);
}
*[ff=title] {
  font-family: system-ui, "Microsoft Yahei", "Segoe UI", -apple-system, Roboto, Ubuntu, "Helvetica Neue", Arial, "WenQuanYi Micro Hei", sans-serif;
}
blockquote {
  display: block;
  margin-left: 0;
  margin-right: 0;
  padding: 0.5rem 0.75rem;
  background: var(--block);
  border-left: 4px solid var(--text-meta);
  border-radius: 4px 6px 6px 4px;
  color: var(--text-p2);
}
:root {
  --blur-px: 12px;
  --blur-bg: rgba(255,255,255,0.5);
}
@media (prefers-color-scheme: dark) {
  :root {
    --blur-bg: rgba(0,0,0,0.5);
  }
}
.blur {
  background: var(--blur-bg);
}
@supports ((-webkit-backdrop-filter: blur(var(--blur-px))) or (backdrop-filter: blur(var(--blur-px)))) {
  .blur {
    background: var(--blur-bg) !important;
    backdrop-filter: saturate(200%) blur(var(--blur-px));
    -webkit-backdrop-filter: saturate(200%) blur(var(--blur-px));
  }
}
.blur:hover {
  background: var(--card);
}
button {
  border: none;
  font-weight: 500;
  outline: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
}
a.button {
  font-weight: 500;
  line-height: 1;
  padding: 0.75rem 2rem;
  border-radius: 3px;
  transition: background 0.2s ease-out;
  -moz-transition: background 0.2s ease-out;
  -webkit-transition: background 0.2s ease-out;
  -o-transition: background 0.2s ease-out;
  font-size: 0.9375rem;
}
a.button.theme {
  background: #1cd0fd;
  color: var(--card);
}
a.button.theme:hover {
  background: #ff5724;
}
a[onclick]:hover {
  cursor: pointer;
}
.cap {
  font-weight: 500;
  font-size: 0.75rem;
  scrollbar-width: none;
  color: var(--text-p3);
}
.cap.blue {
  color: #0d87e9;
}
.cap.cyan {
  color: #03c3f5;
}
.cap.theme {
  color: #03cafc;
}
.dis-select {
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
.mobile-only {
  display: none;
}
@media screen and (max-width: 667px) {
  .mobile-only {
    display: block !important;
  }
}
@media screen and (max-width: 667px) {
  .mobile-hidden {
    display: none !important;
  }
}
.float-panel {
  position: sticky;
  right: 0;
  bottom: 2rem;
  float: right;
  z-index: 10;
  display: flex;
  border-radius: 2rem;
  margin-right: 1rem;
  overflow: hidden;
  --blur-px: 16px;
  --blur-bg: rgba(255,255,255,0.4);
  transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
}
@media (prefers-color-scheme: dark) {
  .float-panel {
    --blur-bg: rgba(0,0,0,0.4);
  }
}
.sidebar-toggle.mobile {
  cursor: pointer;
  color: var(--text-p0);
  background: none;
  padding: 0.5rem;
  line-height: 0;
  font-size: 20px;
  margin: 0;
}
.l_body.mobile.sidebar .float-panel {
  box-shadow: 0 4px 8px 0px rgba(0,0,0,0.1);
  transform: translateY(-2px);
}
.l_body.mobile.sidebar .sidebar-toggle.mobile {
  background: var(--card);
  color: #ff5724;
  border-color: var(--block-border);
}
code {
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
  font-family: Menlo, Monaco, Consolas, system-ui, "Courier New", monospace, sans-serif;
}
p>code:not([class]),
li>code:not([class]) {
  font-size: 85%;
  background: var(--block);
  padding: 0.2em;
  border-radius: 4px;
  color: var(--text-code);
}
.md-text .highlight,
pre:not([class]):has(>code) {
  margin: var(--gap-p) 0;
  border-radius: 6px;
  overflow: hidden;
  background: var(--block);
  line-height: 1.5;
  font-family: Menlo, Monaco, Consolas, system-ui, "Courier New", monospace, sans-serif;
  box-sizing: border-box;
}
@media screen and (min-width: 500px) {
  .md-text .highlight,
  pre:not([class]):has(>code) {
    min-width: 180px;
  }
}
.md-text .highlight {
  position: relative;
  overflow: auto;
  display: block;
}
.md-text .highlight figcaption {
  color: var(--text-p2);
  font-size: 0.8125rem;
  padding: 4px 0.5rem;
  position: sticky;
  left: 0;
  background: var(--block-hover);
  border-top-left-radius: calc(6px - 1px);
  border-top-right-radius: calc(6px - 1px);
}
.md-text .highlight figcaption span {
  margin-right: 4px;
}
.md-text .highlight >table {
  overflow: auto;
  display: block;
  margin: 0 !important;
  background: transparent;
  border: none;
}
.md-text .highlight >table td,
.md-text .highlight >table th {
  padding: 0;
  border: none;
  line-height: 1.5;
}
.md-text .highlight >table::-webkit-scrollbar {
  height: 0px;
  width: 0px;
}
.md-text .highlight >table::-webkit-scrollbar-track-piece {
  background: transparent;
}
.md-text .highlight >table::-webkit-scrollbar-thumb {
  background: transparent;
  cursor: pointer;
  border-radius: 6px;
}
.md-text .highlight >table:hover::-webkit-scrollbar-thumb {
  background: var(--text-meta);
}
.md-text .highlight >table:hover::-webkit-scrollbar-thumb:hover {
  background: var(--text-p3);
}
.md-text .highlight >table tr {
  background: transparent;
}
.md-text .highlight >table tr:hover {
