* {
  outline: none;
}
html {
  font-family: HarmonyOS_Regular, system-ui, "Microsoft Yahei", "Segoe UI", -apple-system, Roboto, Ubuntu, "Helvetica Neue", Arial, "WenQuanYi Micro Hei", sans-serif;
  font-size: 16px;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  scroll-behavior: smooth;
}
body {
  background: var(--site-bg);
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizelegibility;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
div.lazy.img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
}
input {
  background: none;
  border: none;
}
input.copy-area {
  display: block;
  font-family: Menlo, Monaco, Consolas, system-ui, "Courier New", monospace, sans-serif;
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--text-p3);
}
svg.loading {
  display: block;
  position: absolute;
  color: var(--text-p3);
  z-index: -1;
  width: 100%;
  height: 2rem;
  margin: auto;
  animation: spin infinite 2s;
  animation-timing-function: linear;
}
@-moz-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.loading-wrap {
  margin: 0;
  text-align: center;
  background: var(--block);
  border-radius: 12px;
  position: relative;
  padding: 2rem;
}
.loading-wrap svg {
  margin: 4px;
}
.loading-wrap:after {
  content: '';
  color: var(--text-p1);
  display: block;
  font-size: 14px;
}
.loading-wrap.error:after {
  content: '';
}
pre:not([class]):has(>code) {
  display: block;
  padding: 1rem;
  overflow: auto;
}
pre:not([class]):has(>code) code {
  padding: 0;
}
span.dot,
span.sep {
  font-size: 0.9em;
  margin: 0 0.25em;
}
span.dot:before {
  content: '';
  font-weight: 900;
}
span.sep:before {
  content: '/';
  padding-left: 2px;
  padding-right: 2px;
}
svg.icon {
  width: 1em;
  height: 1em;
  vertical-align: middle;
  fill: currentColor;
  overflow: hidden;
}
h1,
.h1 {
  font-size: 1.75rem;
  font-weight: 700;
}
@media screen and (max-width: 500px) {
  h1,
  .h1 {
    font-size: 1.75rem;
  }
}
h2,
.h2 {
  font-size: 1.5rem;
}
h3,
.h3 {
  font-size: 1.375rem;
}
h4,
.h4 {
  font-size: 1.125rem;
}
h5 {
  font-size: 0.9375rem;
}
h6 {
  font-size: 0.75rem;
}
.fs14 {
  font-size: 0.875rem;
}
.fs14 p {
  font-size: 0.875rem !important;
}
.fs14 li {
  font-size: 0.875rem !important;
}
.fs12 {
  font-size: 0.75rem;
}
.widgets .post-title {
  margin: 0.75rem 0;
  line-height: 1.2;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.widgets .post-title .cap {
