}
div.toast {
  max-width: 60%;
  padding: 1rem 3rem;
  line-height: 1.5;
  color: var(--text-p1);
  font-weight: 500;
  text-align: center;
  border-radius: 12px;
  background: var(--card);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  box-shadow: 0 4px 8px 0px rgba(0,0,0,0.1);
}
